*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

.receptsMenu{
    display: flex;
    position: relative;
    top: 0;
    left: 0;
    width: 200vw;
    height: 100%;
    font-family: 'Open Sans', sans-serif;
    animation-name: swingRight;
    animation-duration: .1s;
}

.receptsMenu.rightSideElo{
    left: -100%;
    animation-name: swingLeft;
    animation-duration: .1s;
}

@keyframes swingLeft{
    0% {left: 0}
    100% {left: -100%}
}

@keyframes swingRight{
    0% {left: -100%}
    100% {left: 0}
}

.leftSide{
    width: 100vw;
    padding: 0 10px;
    position: relative;
    top: 0;
    left: 0;
}

.createReceptButton{
    color: #D1B764;
    border: none; 
    background-color: white;
    font-size: 50px;
    position: absolute;
    bottom: 0;
    right: 10px;
}

.searchReceptSection{
    height: 20%;
    padding: 20px 0;
    text-align: left;
    border-bottom: 2px solid #000
}

@media (max-width: 321px) and (orientation: portrait) {
    .searchReceptSection{
        height: 30%;
    }
}

.searchReceptSection form{
    margin: 5px 0;
}

.searchReceptSection form label{
    padding-right: 10px;
}

.searchRecept{
    width: 200px;
    padding: 5px 0;
    border: 1px solid black;
}

.setMealType{
    width: 90px;
    padding: 5px 0;
    border: 1px solid black; 
}

.setPeopleNumber{
    width:35px;
    padding: 5px 0;
    border: 1px solid black;
}

.receptList{
    list-style: none;
    overflow: scroll;
    height: 85%;
    padding-bottom: 200px;
}

.receptListItem{
    display: flex;
    flex-direction: column;
    padding: 15px 0; 
    border-bottom: 1px solid #333
}

.receptListItem:nth-child(odd){
    background-color: #eee;
}

.receptListItemName{
    font-weight: bold;
    padding-bottom: 10px;
}

.listButtonSection{
    display: flex;
    justify-content: space-around;
}

.receptListItemButton{
    flex-basis: 45%;
    background-color: #D1B764;
    padding: 5px 5px;
    border: 1px solid #333; 
    font-family: 'Open Sans', sans-serif;
    color: #333;
    text-decoration: none;
    font-size: 12px;
}


.rightSide{
    width: 100vw;
    padding: 0 10px;
}